.subscribe {
  padding-bottom: 100px;
  position: relative;

  // display: flex;
  // flex-wrap: wrap;
  // flex-direction: column;
  // justify-content: center;
  // align-items: center;
}

.subscribe {
  @media screen and (max-width: 767px) {
    padding-bottom: 60px;
  }
}

.subscribe {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row-reverse;
  }
}

.subscribe__img {
  margin-bottom: 17px;
  margin-right: 0;
}

.subscribe__img {
  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
  }
}

.subscribe__title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;

  color: #2c2c2c;

  margin-bottom: 20px;
}

.subscribe__title {
  @media screen and (max-width: 767px) {
    font-size: 28px;
    width: 280px;
  }
}

.subscribe__desc {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;

  color: #2c2c2c;

  margin-bottom: 24px;
  margin-left: 0;
  width: 280px;
}

.subscribe__desc {
  @media screen and (min-width: 768px) {
    font-size: 16px;
    width: 656px;
    line-height: 150%;
  }
}

.subscribe__input {
  box-sizing: border-box;

  max-width: 278px;
  width: 100%;
  height: 42px;

  border: 0.9px solid #ffe550;
  border-radius: 30px;

  background-color: #fefefe;

  padding: 12px 11px;

  outline: none;
}

.subscribe__input {
  @media screen and (max-width: 767px) {
    margin: 0 auto;
  }
}

.subscribe__input {
  @media screen and (min-width: 768px) {
    max-width: 394px;
  }
}

.subscribe__mail {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  position: relative;
}

.subscribe__mail {
  @media screen and (max-width: 767px) {
    max-width: 278px;
  }
}

.subscribe__mail {
  @media screen and (min-width: 768px) {
    flex-wrap: nowrap;
    flex-direction: row;
  }
}

.subscribe__input::placeholder {
  font-weight: 400;
  font-size: 12px;
  line-height: 150%;

  color: #505050;
}

.subscribe__input:valid {
  border-color: #7eb101;
}

.subscribe__input:invalid {
  border-color: #ff705d;
}

.subscribe__notification {
  display: flex;
  align-items: center;
  opacity: 0;

  position: absolute;
  left: 0;

  bottom: -20px;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.subscribe__icon {
  fill: #ff705d;

  margin-right: 4px;
}

.subscribe__icon .subscribe__icon--error {
  opacity: 0;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.subscribe__notification--text {
  font-weight: 400;
  font-size: 10px;
  line-height: 150%;

  color: #ff705d;
}

.subscribe__input:invalid:not(:placeholder-shown) ~ .subscribe__notification,
.subscribe__icon .subscribe__icon--error {
  opacity: 1;
}

.subscribe__button {
  font-family: inherit;
  background-color: #bbf330;
  color: #2c2c2c;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  cursor: pointer;

  border-radius: 30px;
  border: none;

  width: 106px;
  height: 42px;

  margin-left: 32px;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.subscribe__button:hover,
.subscribe__button:focus {
  background-color: #b5ff00;
}

.subscribe__button {
  @media screen and (max-width: 767px) {
    margin-left: auto;
    margin-top: 20px;
  }
}

